* {
	margin: 0;
	padding: 0;
	color: rgb(30, 20, 10);
	font-family: 'Trebuchet MS', 'Arial', sans-serif;
}

.previewImg {
	float: left;
	margin-right: 20px;
}

.content {
	display: flex;
	flex-direction: column;
	height: 100%;
	justify-content: space-evenly;
	font-size: 17px;
}

.card {
	margin: 10px auto;
	padding: 15px;
	width: 80%;
	max-width: 500px;
	height: 120px;
	position: relative;
	text-align: left;
	border-bottom: 1.5px solid rgb(90, 80, 70);
	cursor: pointer;
}

.describe {
	font-size: 14px;
	color: rgb(80, 70, 60);
	text-overflow: ellipsis;
}

.describeContent {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.nonDrag {
	user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
}
