* {
	margin: 0;
	padding: 0;
	color: rgb(30, 20, 10);
	font-family: 'Trebuchet MS', 'Arial', sans-serif;
}

.page {
	display: flex;
	flex-direction: row;
	margin: 5vh 0;
	position: absolute;
	left: 50%;
	transform: translate(-50%, 0);
	padding: 15px;
	width: 82%;
	max-width: 1200px;
	text-align: left;
	overflow: hidden;
	border-bottom: 1.5px solid rgb(90, 80, 70);
}

.info {
	overflow-x: auto;
	white-space: nowrap;
	padding-bottom: 16px;
	position: relative;
	width: 100%;
}

.describe {
	white-space: nowrap;
	font-size: 14px;
	padding-bottom: 16px;
	padding-right: 6px;
	color: rgb(80, 70, 60);
}

.wrapper {
	display: flex;
	align-items: flex-end;
}

.readButton {
	position: fixed;
	bottom: 30px;
	right: 30px;
	height: 60px;
	width: 160px;
	border: 2px solid rgb(30, 20, 10);
	font-size: 23px;
	color: rgb(30, 20, 10);
	background-color: rgba(245, 244, 240, 0.9);
	z-index: 27;
}

.reversedReadButton:hover {
	position: fixed;
	bottom: 30px;
	right: 30px;
	height: 60px;
	width: 160px;
	border: 2px solid rgb(30, 20, 10);
	font-size: 23px;
	color: rgb(245, 244, 240);
	background-color: rgb(30, 20, 10);
	z-index: 27;
}

.previewImg {
	float: left;
}

.content {
	display: inline-block;
	font-size: 18px;
	width: calc(100% - 384px);
}

.pressed {
	transform: translate(0, 5px);
}

.nonDrag {
	user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
}

@media screen and (max-width: 600px) {
	/* * {
		background-color: rgb(242, 209, 209);
	} */

	.page {
		flex-direction: column;
		align-items: center;
		margin: 0;
		padding: 0 20px;
	}

	.content {
		width: 100%;
	}

	.previewImg {
		display: block;
		margin: 30px 0;
		float: none;
		width: 100%;
		height: 100%;
	}
}
