* {
	margin: 0;
	padding: 0;
	background-color: rgb(245, 244, 240);
	color: rgb(30, 20, 10);
	font-family: 'Trebuchet MS', 'Arial', sans-serif;
}

::placeholder {
	color: rgb(100, 90, 80);
}

::-ms-input-placeholder {
	color: rgb(100, 90, 80);
}

.searchBar {
	margin-bottom: 50px;
	display: inline-block;
	font-size: 23px;
	text-align: center;
	height: 35px;
	width: 60%;
	max-width: 350px;
	min-width: 150px;
	outline: none !important;
	box-shadow: none !important;
	background-color: transparent;
	border: none;
	border-bottom: 2px solid;
}

.navLogo {
	width: 40px;
	height: 40px;
	margin: 5px;
}

.navItem {
	font-size: 18px;
	position: absolute;
	top: 50%;
	left: 60px;
	background-color: transparent;
	transform: translate(0, -50%);
}

nav {
	position: sticky;
	top: 0;
	display: flex;
	justify-content: start;
	background-color: rgb(245, 244, 240, 0.5);
	border-bottom: 1px dashed rgb(90, 80, 70);
	backdrop-filter: blur(8px);
	-webkit-backdrop-filter: blur(8px);
	margin-bottom: 20px;

	z-index: 2727;
}

.nonDrag {
	user-select: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
}
